e:transform;display:inline-block}.l1 span{transform-origin:bottom;animation:linksApparition 1.3s 3.8s cubic-bezier(0.77, 0, 0.175, 1) backwards}.l2 span{transform-origin:top;transform:translateY(100%) rotateX(-90deg)}.l2{position:absolute;top:0;left:0}.container-contact-links-linkedin:hover .l1 span{transform:translateY(-100%) rotateX(-90deg)}.container-contact-links-linkedin:hover .l2 span{transform:translateY(0%) rotateX(0deg)}.container-contact-links-gmail{position:relative}.container-contact-links-gmail span{color:#333;font-size:clamp(10px, 10vw, 40px);transition:0.5s;font-weight:100;will-change:transform;display:inline-block}.gm1 span{transform-origin:bottom;animation:linksApparition 1.3s 4.5s cubic-bezier(0.77, 0, 0.175, 1) backwards}.gm2 span{transform-origin:top;transform:translateY(100%) rotateX(-90deg)}.gm2{position:absolute;top:0;left:0}.container-contact-links-gmail:hover .gm1 span{transform:translateY(-100%) rotateX(-90deg)}.container-contact-links-gmail:hover .gm2 span{transform:translateY(0%) rotateX(0deg)}a{color:#333;text-decoration:none}.bar-contact1{background:#333;height:50%;width:2px;animation:linksApparition 1.1s 3.6s cubic-bezier(0.77, 0, 0.1, 1) backwards}.bar-contact2{background:#333;height:50%;width:2px;animation:linksApparition 1.1s 4.3s cubic-bezier(0.77, 0, 0.1, 1) backwards}@media screen and (max-width: 800px){.container-keeptouch{display:grid;grid-template-columns:1fr;grid-template-rows:5fr 6fr}.container-grid-links-contact{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;flex-direction:column}.bar-contact1{background:#333;height:2px;width:50%}.bar-contact2{background:#333;height:2px;width:50%}}@keyframes stalkMeApparition{from{transform:translateX(900px)}to{transform:none}}@keyframes linksApparition{from{transform:translateY(300px) rotate(75deg)}to{transform:none}}

