.yz {
    height: 9vh;
    width: 100%;
    // background-color: #f5efdf;
    background-color: #f8f8f8;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-template-areas: "1" "2";
    font-family: 'Roboto', cursive, sans-serif;
    overflow: hidden;
}
.footer-network {
    overflow: hidden;
    grid-area: "1";
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateY(100px) rotate(70deg);
    animation: footerApparition 1s 5.2s ease-out forwards;
}
.footer-network > p {
    font-size: clamp(10px, 6vw, 28px);
    font-weight: 800;
}
.localisation {
    grid-area: "2";
    overflow: hidden;
}
@keyframes footerApparition {
    100% {
      transform: none;
      opacity: 1;
    }
  }
  //////// media queries ///////
  @media screen and (max-width:800px) {
      .footer-network {
          img {
              width: 20px;
              height: 20px;
          }
      }
  }




