@mixin midFlex {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container-keeptouch {
  overflow: hidden;
    width: 100%;
    height: 82vh;
    background: #F8F8F8;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 6fr 4fr;
    grid-template-areas: "1" "2";
    font-family: "Ogg",'Sawarabi Gothic', sans-serif;
}
.contactez-moi {
    grid-area: "1";
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.title {
    position: relative;
  }
  .title span {
    font-size: clamp(60px, 11vw, 150px);
    transition: 0.5s;
    font-weight: 100;
    will-change: transform;
    display: inline-block;
  }
  .span-container {
    overflow: hidden;
  }
  .s1 span {
    transform-origin: bottom;
    animation: stalkMeApparition 1.5s 2.5s cubic-bezier(0.77, 0, 0.175, 1) backwards;
  }
  .s2 span {
    transform-origin: top;
    transform: translateY(100%) rotateX(-90deg);
  }
  .s2 {
    position: absolute;
    top: 0;
    left: 0;
  }
  .title:hover .s1 span {
    transform: translateY(-100%) rotateX(-90deg);
  }
  .title:hover .s2 span {
    transform: translateY(0%) rotateX(0deg);
  }

  //////////////   Links CONTACT   //////////////
  .links-contact {
      grid-area: "2";
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      font-family: 'Roboto', sans-serif;
      font-family: "Ogg",'Sawarabi Gothic', sans-serif;

      .container-grid-links-contact {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
      }
  }  
  ///////// --------- GITHUB link ----------- //////
  .container-contact-links-github {
    position: relative;
  }
  .container-contact-links-github span {
    color: #333;
    font-size: clamp(10px, 10vw, 40px);
    transition: 0.5s;
    font-weight: 100;
    will-change: transform;
    display: inline-block;
  }
  
  .g1 span {
    transform-origin: bottom;
    animation: linksApparition 1.3s 3.3s cubic-bezier(0.77, 0, 0.175, 1) backwards;
  }
  .g2 span {
    transform-origin: top;
    transform: translateY(100%) rotateX(-90deg);
  }
  .g2 {
    position: absolute;
    top: 0;
    left: 0;
  }
  .container-contact-links-github:hover .g1 span {
    transform: translateY(-100%) rotateX(-90deg);
  }
  .container-contact-links-github:hover .g2 span {
    transform: translateY(0%) rotateX(0deg);
  }

  ///////// -------- LINKEDIN link ---------- //////
