  }
  a {
      color: #333;
      text-decoration: none;
  }

  ////// -------  BAR  ------- /////
  .bar-contact1 {
      background: #333;
      height: 50%;
      width: 2px;
      animation: linksApparition 1.1s 3.6s cubic-bezier(0.77, 0, 0.100, 1) backwards;
  }
  .bar-contact2 {
      background: #333;
      height: 50%;
      width: 2px;
      animation: linksApparition 1.1s 4.3s cubic-bezier(0.77, 0, 0.100, 1) backwards;
  }

  ///////////////////  ------  MEDIA QUERIES ------   ///////////////
  @media screen and (max-width: 800px) {
      .container-keeptouch {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 5fr 6fr;
      }
      .container-grid-links-contact {
        @include midFlex();
        flex-direction: column;
      }
      .bar-contact1 {
        background: #333;
        height: 2px;
        width: 50%;
    }
      .bar-contact2 {
        background: #333;
        height: 2px;
        width: 50%;
    }
  }

  @keyframes stalkMeApparition {
    from {
      transform: translateX(900px);
    }
    to {
      transform: none;
    }
  }
  @keyframes linksApparition {
    from {
      transform: translateY(300px) rotate(75deg);
    }
    to {
      transform: none;
    }
  }